@import url(https://fonts.googleapis.com/css?family=Raleway:400,300,700);


body{
  background: url(/sofiweb/static/media/back.f7c8686f.jpeg) no-repeat center center fixed;
}

div.Nav-container{
  position: relative;
  display: flex;
  height: 89px;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
}

.navbar{
font-family: 'Source Sans Pro',sans-serif;
font-weight: 500;
font-style: normal;
text-transform: uppercase;
width: 430px;
height: 21px;
position: absolute;
right: 61%;
font-size: 20px
}

ul.horizontal{
  text-align: center;
  margin: 2px;
  padding: 5px;
  list-style: none;
}

li.topli{
  margin: 0;
  display: inline-block;
  width: auto;
  height: auto;
  list-style: none;
}

.containsNavContents{
  margin: 0;
  padding: 0;
  list-style: none;
}

a.top{
  padding-left: 33px;
  color: black;
  text-decoration: none;
}

.player{
  position: relative;
  width: 45%;
  height:42%;
  display: flex;
  opacity: 0.7;
  left: 59%;
  top: 20px;
}

.headercontent{
  position: absolute;
  width: 60%;
  z-index: 2;
  top: 0px;
  left: 30%;
}

@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}*,*:before,*:after{box-sizing:border-box}.music-player-container{display:inline-block;height:316px;position:absolute;min-width:460px;z-index:1;top:58px;left:0px}.music-player-container:after{-webkit-filter:blur(8px);-ms-filter:blur(8px);filter:blur(8px);background-color:rgba(0,0,0,0.8);bottom:105px;content:' ';display:block;height:10px;left:19px;position:absolute;transform:rotate(-3deg);width:70%;z-index:0}.music-player{background-color:#fff;height:200px;padding:40px 250px 40px 18px;position:absolute;text-align:right;width:460px;z-index:3;border-radius:10%}.player-content-container{transform:translateY(-50%);top:52%;position:relative}.artist-name{font-size:20px;font-weight:normal;margin:0 0 0.75em 0}.album-title{font-weight:200;font-size:20px;margin:0 0 1.75em 0}.song-title{font-size:14px;font-weight:200;margin:0 0 1.5em 0}.album{box-shadow:3px 3px 15px rgba(0,0,0,0.65);height:190px;margin-left:233px;position:relative;width:187px;z-index:10}.album-art{background:#fff url(/sofiweb/static/media/whatsup.34812b22.gif) center/cover no-repeat;height:190px;position:relative;width:187px;z-index:10}.vinyl{animation:spin 2s linear infinite;transition:all 500ms;background-image:url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/83141/vinyl.png"),url(/sofiweb/static/media/whatsup.34812b22.gif);background-position:center, center;background-size:cover, 40% auto;background-repeat:no-repeat;border-radius:100%;box-shadow:0 0 10px rgba(0,0,0,0.8);height:190px;left:0;position:absolute;top:5px;width:190px;z-index:5;will-change:transform, left}.is-playing .vinyl{left:52%}.music-player-controls{text-align:center}[class^="control-"]{-webkit-filter:brightness(95%);-ms-filter:brightness(95%);filter:brightness(95%);border-radius:100%;display:inline-block;height:44px;margin:0 3px;width:44px}[class^="control-"]:hover{-webkit-filter:brightness(85%);-ms-filter:brightness(85%);filter:brightness(85%);cursor:pointer}.control-play{background:transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/83141/play.svg") center/cover no-repeat}.is-playing .control-play{background:transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/83141/pause.svg") center/cover no-repeat}.control-forwards{background:transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/83141/forwards.svg") center/cover no-repeat}.control-back{background:transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/83141/backwards.svg") center/cover no-repeat}@keyframes infinite-spinning{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

